 .cursor {
 	pointer-events: none;
 	.cursor__ball {
 		position: fixed;
 		top: 0;
 		left: 0;
 		// mix-blend-mode: difference;
 		z-index: 1000000;
 		circle {
 			fill: none;
 			stroke: white;
 			stroke-width: 1;
 		}
