    .top3 {
        display: -webkit-box;
        display: -ms-flexbox;
        display: flex;
        -webkit-box-orient: horizontal;
        -webkit-box-direction: normal;
        -ms-flex-flow: row wrap;
        flex-flow: row wrap;
        -webkit-box-pack: center;
        -ms-flex-pack: center;
        justify-content: center;
        -ms-flex-line-pack: center;
        align-content: center;
        -webkit-box-align: center;
        -ms-flex-align: center;
        align-items: center;

        &>div {
            padding: 0 6rem;
            position: relative;
            max-width: 100%;
            max-height: 100%;
        }

        svg {
            // fill: $black-degraded;
            stroke: $white;
            // stroke-width: .1rem;
            // stroke-miterlimit: 10;
            // filter: blur(59px);
            position: relative;
            z-index: 1;
        }

        img {
            width: 15.625rem;
            height: 12.5625rem;
            position: absolute;
            top: 6.125rem;
            z-index: 0;
        }

        .second img {
            right: 0;
        }

        .first img {
            right: 0;
            top: 10%;
        }

        .third img {
            left: 0;
        }

        p {
            padding-top: 2rem;
        }
    }

    .board {
        position: relative;

        table {
            max-width: 62.75rem;
            margin: 10rem 2rem 0 2rem;
            background-color: $body-bg;
            border: thin solid $white;

            thead {
                display: block;
                border-bottom: thin solid $white;
                padding: 1.4rem 0;
            }

            tbody {
                padding-top: 2rem;
                display: block;
                max-height: 37.0625rem;
                overflow-y: auto;
            }

            td,
            th {
                width: 60rem; // Taille d'une colonne
                text-align: center;
                padding: 1rem 0;
            }
        }
    }

    .hand {
        position: absolute;
        bottom: -12%;
        -webkit-transform: translateY(12%);
        -ms-transform: translateY(12%);
        transform: translateY(12%);
        right: -11%;
        -webkit-transform: translateX(11%);
        -ms-transform: translateX(11%);
        transform: translateX(11%);

        img {
            width: 80%;
        }
    }
}
