/* Bouton générale */
.btn {
    text-align: center;
    font-family: $font-logo;
    text-transform: lowercase;
    border-radius: 30rem;
    padding: 1rem 3.5rem;
    color: $white;
    display: inline-block;
    border: .2rem solid $black;
    position: relative;
    overflow: hidden;
    z-index: 1;
    line-height: 100%;
    // Permet l'animation du btn
    background-size: 200% auto;
    -webkit-transition: 0.5s;
    -o-transition: 0.5s;
    transition: 0.5s;
    -webkit-transition: all 0.3s cubic-bezier(.25, .8, .25, 1);
    -o-transition: all 0.3s cubic-bezier(.25, .8, .25, 1);
    transition: all 0.3s cubic-bezier(.25, .8, .25, 1);
}

.btn.focus,
.btn:focus,
.btn:hover {
    // Glassmorphism
    background-image: -webkit-linear-gradient($black-degraded);
    background-image: -moz-linear-gradient($black-degraded);
    background-image: -o-linear-gradient($black-degraded);
    background-image: linear-gradient($black-degraded);
    background-position: right center;
    // Impression qu'on appuie sur le btn
    -webkit-box-shadow: 0 4px 10px 0 rgba(0, 0, 0, .2), 0 4px 20px 0 rgba(0, 0, 0, .1);
    box-shadow: 0 4px 10px 0 rgba(0, 0, 0, .2), 0 4px 20px 0 rgba(0, 0, 0, .1);
    color: $white;
    border: .2rem solid $white;
}

