// Secondaire - Secondary
// Succès - Succes
// Echec - Danger
// Attention - Warning

/* Bouton générale */
// Effet de glitch
.btn-animation_glitch {
    display: block;
    position: relative;

    // background: none;
    &:before,
    &:after {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        padding: 8px 0;
        width: 100%;
        -webkit-clip: rect(0px, 0px, 0px, 0px);
        clip: rect(0px, 0px, 0px, 0px);
        background: $black;
    }

    &:before {
        left: -3px;
        top: -2px;
        text-shadow: 2px 0 $black;
        box-shadow: 2px 0 $black;
    }

    &:after {
        left: 2px;
        bottom: -2px;
        text-shadow: -.1rem 0 $black;
        box-shadow: -.1rem 0 $black;
    }

    &:hover:before {
        animation: glitch-test 1.5s infinite linear alternate-reverse;
    }

    &:hover:after {
        animation: glitch-test 2s infinite linear alternate;
    }
}

@keyframes glitch-test {
    0% {
        clip: rect(-3px, 600px, 0px, 0px);
    }

    5.88235% {
        clip: rect(0px, 600px, 0px, 0px);
    }

    11.76471% {
